
        import {store, MERGED_BUNDLES_NAME} from 'bundleStore';
        
        let bundle;
        
        if ('jasperreports_messages' !== MERGED_BUNDLES_NAME) {
            store['jasperreports_messages'] = store['jasperreports_messages'] || {};
            bundle = store['jasperreports_messages'];
        } else {
            bundle = Object.keys(store).reduce((acc, key) => {
                return {
                    ...acc,
                    ...store[key]
                }
            }, {});
        }
                                     
        export default bundle;
    